.portfolio-section {
	padding: 121px 0;
	background-color: var(--light-color);

	@include small-tablet {
		padding-top: 60px;
		padding-bottom: 60px;
	}

	@include mobile {
		padding-top: 20px;
	}

	&__container {
		overflow: hidden;
	}

	&__top {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		margin-bottom: 47px;

		@include mobile {
			display: block;
		}
	}

	&__title {
		margin: 0;
		padding-bottom: 12px;
		max-width: 710px;

		@include mobile {
			padding-bottom: 0;
			margin-bottom: 20px;
			max-width: none;
		}
	}

	&__items {
		margin: -100px;
		padding: 100px;

	}

	&__learn {
		margin-top: 80px;
	}

	.portfolio-item__title {
		font-size: 18px;
	}
}