.nav {

	&__list {
		display: flex;

		@include tablet {
			margin-bottom: 40px;
			display: block;
		}
	}

	&__item {
		&:not(:last-child) {
			margin-right: 41px;

			@include tablet {
				margin-right: 0;
				margin-bottom: 30px;
			}
		}
	}

	&__link {
		font-weight: 700;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray800-color);
		transition: color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--orange-color);
			outline-offset: 3px;
		}

		&:hover {
			color: var(--orange-color);
		}

		&:active {
			color: var(--active-color);
		}

		&--current {
			color: var(--orange-color);
		}

		@include tablet {
			font-weight: 900;
			font-size: 24px;
		}
	}
}