.site-modal {
	padding: 48px;
	border-radius: 4px;
	width: 486px;

	.graph-modal__close {
		top: 24px;
		right: 24px;
		width: 24px;
		height: 24px;
		background-image: url('/img/close-modal.svg');

		&:focus {
			outline: 1px solid var(--orange-color);
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 24px;
		font-weight: 700;
		font-size: 28px;
		line-height: 150%;
		text-align: center;
		color: var(--gray900-color);

		@include mobile {
			font-size: 26px;
		}
	}

	&__form {
		.form-field__input--modal {
			height: 86px;
		}
	}

	&__field {
		&:not(:last-child) {
			margin-bottom: 24px;
		}
	}

	&__btn {
		margin-top: 20px;
		width: 100%;
	}

	@include mobile {
		padding: 20px;
		padding-top: 40px;
		width: 90%;
	}
}

.choices {
	width: 100%;

	&:focus {
		outline: 1px solid var(--orange-color);
	}
}

.choices__inner {
	padding: 0;
	margin: 0;
	padding: 13px 16px;
	border-radius: 4px;
	border: 1px solid #D7DADD;
	height: 52px;
	background: #F4F5F7;
}

.choices__list--single .choices__placeholder {
	font-size: 16px;
	color: var(--gray700-color);
	opacity: 1;
}

.choices[data-type*=select-one]:after {
	border: none;
	right: 18px;
	width: 14px;
	height: 7px;
	background-image: url('/img/angle-down.svg');
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	transform: translateY(-50%);
	transition: transform 0.3s ease-in-out;
}

.choices[data-type*=select-one].is-open:after {
	transform: rotate(180deg) translateY(-50%);
}

.choices__list--dropdown .choices__item--selectable:after {
	display: none;
}

.choices__list--dropdown {
	background-color: var(--light-color);
}

.custom-file {
	cursor: pointer;

	&__input {
		position: absolute;
		opacity: 0;
	}

	&__text {
		position: relative;
		display: inline-block;
		padding-left: 36px;
		font-size: 14px;
		line-height: 150%;
		color: var(--gray800-color);
		background-image: url('/img/file-icon.svg');
		background-size: 24px 24px;
		background-position: left center;
		background-repeat: no-repeat;
	}

	&__input:focus+&__text {
		outline: 1px solid var(--orange-color);
	}
}