.know-form {
	position: relative;
	top: 92px;
	padding: 48px 60px;
	border-radius: 4px;
	border: 1px solid #000000;
	box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
	background-color: var(--light-color);

	&__title {
		margin: 0;
		margin-bottom: 32px;
		font-weight: 700;
		font-size: 28px;
		line-height: 150%;
		text-align: center;
		color: var(--gray900-color);

		@include mobile {
			font-size: 24px;
		}
	}

	&__content {
		display: flex;
		align-items: flex-end;

		&>:not(:last-child) {
			margin-right: 24px;
		}

		&__field {
			width: 100%;
		}

		&>* {
			margin-right: 0 !important;
			margin-bottom: 20px;
			width: 49%;
			flex-grow: initial;
		}

		@include tablet {
			justify-content: space-between;
			flex-wrap: wrap;
		}

		@include mobile {
			display: block;
		}
	}

	@include mobile {
		padding: 20px;
	}
}

.form-field {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	max-width: 100%;

	&__caption {
		display: inline-block;
		margin-bottom: 10px;
		font-weight: 400;
		font-size: 14px;
		line-height: 150%;
		color: var(--gray800-color);
	}

	&__input {
		padding: 12px 16px;
		border: 1px solid #D7DADD;
		width: 260px;
		height: 44px;
		border-radius: 4px;
		font-weight: 400;
		font-size: 14px;
		line-height: 150%;
		background: var(--gray200-color);
		resize: none;

		&::placeholder {
			color: #9A9CA5;
		}

		@include tablet {
			width: 100%;
		}
	}

	&:focus {
		outline: 2px solid rgba(255, 90, 48, 0.4);
		box-shadow: 0px 80px 80px -20px rgba(255, 90, 48, 0.08), 0px 30px 24px -10px rgba(255, 90, 48, 0.05), 0px 12px 10px -6px rgba(255, 90, 48, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
	}

	&--big {
		width: 100%;

		.form-field__input {
			width: 100%;
		}

		@include tablet {
			width: 49%;
		}
	}

	&--bigray {
		padding-top: 10px;
		height: 66px;
		resize: none;
	}

	&__btn {
		padding: 11px 31px;
		font-size: 14px;

		@include mobile {
			margin-top: 30px;
		}
	}

	&--gray {
		width: 100%;

		.form-field__input {
			padding-top: 9px;
			width: 100%;
			background-color: var(--gray200-color);
		}
	}

	&--footgray {
		width: 100%;

		.form-field__input {
			padding-top: 9px;
			width: 100%;
			border: 1px solid rgba(255, 255, 255, 0.2);
			border-radius: 4px 0px 0px 4px;
			background-color: rgba(255, 255, 255, 0.12);
		}
	}

	@include mobile {
		width: auto;
	}
}

.contacts-form__field {
	@include mobile {
		width: 100%;
	}
}