.header {
	padding-top: 23px;
	padding-bottom: 21px;

	&__container {
		@include flex-all-sb;
	}

	&__logo {
		&:focus {
			outline: 2px solid var(--orange-color);
			outline-offset: 2px;
		}
	}

	&__nav {
		margin-left: -114px;

		@include mini-desktop {
			margin-left: 0;
		}

		@include tablet {
			position: fixed;
			left: 0;
			top: 0;
			z-index: 1000;
			display: block;
			overflow-y: auto;
			padding: 50px;
			width: 80%;
			height: 100vh;
			background-color: var(--gray200-color);
			transform: translateX(-150%);
			transition: transform 0.3s ease-in-out;

			&--active {
				transform: none;
			}
		}

		.header-contacts__link {
			@include tablet {
				display: flex;
				margin: 0;
				margin-bottom: 30px;
			}
		}

		@include mobile {
			padding: 20px;
		}
	}
}

.burger {
	@include burger;
}

.header--main {
	background-color: var(--light-color);
}

.header--secondary {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
}