.we-offer {
	padding: 120px 0 163px 0;
	background-color: var(--light-color);

	&__container {
		display: grid;
		grid-template-columns: repeat(12, 1fr);
		column-gap: 105px;

		@include mini-desktop {
			column-gap: 30px;
		}

		@include small-tablet {
			display: block;
		}
	}

	&__image {
		grid-column: 7 span;
		width: 705px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		@include big-desktop {
			width: 100%;
		}

		@include small-tablet {
			margin-bottom: 30px;
		}
	}

	&__content {
		grid-column: 5 span;
		padding-top: 5px;
	}

	&__title {
		margin: 0;
		margin-bottom: 55px;
		font-family: 'Lato', sans-serif;
		font-weight: 900;

		@include small-tablet {
			margin-bottom: 30px;
		}
	}

	@include tablet {
		padding-top: 80px;
		padding-bottom: 80px;
	}

}

.accordion {

	&:not(:last-child) {
		margin-bottom: 8px;
	}

	&__control {
		position: relative;
		padding-left: 44px;

		&:focus {
			outline: 2px solid var(--orange-color);
			outline-offset: 2px;
		}
	}

	&__title {
		display: block;
		margin: 0;
		font-weight: 700;
		font-size: 28px;
		line-height: 150%;
		color: var(--gray900-color);
		text-align: left;

		@include tablet {
			font-size: 24px;
		}
	}

	&__icon {
		position: absolute;
		left: 0;
		top: 10px;
		display: block;
		width: 22px;
		height: 22px;

		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 50%;
			width: 100%;
			height: 2px;
			border-radius: 100px;
			background-color: var(--orange-color);
			transform: translateY(-50%);
		}

		&::after {
			content: '';
			position: absolute;
			left: 50%;
			top: 0;
			width: 2px;
			height: 100%;
			border-radius: 100px;
			background-color: var(--orange-color);
			transform: translateX(-50%);
			transition: opacity 0.3s ease-in-out;
		}
	}

	&__content {
		padding: 15px 0;
		padding-left: 44px;
		padding-right: 20px;

		p {
			margin: 0;
			font-weight: 400;
			font-size: 16px;
			line-height: 160%;
			color: var(--gray700-color);

			@include tablet {
				font-size: 14px;
			}
		}
	}
}

.is-open .accordion__icon {
	transform: none;

	&::after {
		opacity: 0;
	}
}