.history {
	padding-top: 180px;
	padding-bottom: 100px;
	background-color: var(--gray200-color);

	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 62px;

		@include small-tablet {
			margin-bottom: 40px;
		}
	}

	&__title {
		margin: 0;
	}

	&__content {
		@include grid;

		@include small-tablet {
			display: block;
		}
	}

	&__item {
		overflow: hidden;
		grid-column: 8 span;
	}

	&__image {
		margin-bottom: 33px;
	}

	&__descr {
		margin: 0;
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray800-color);
	}

	.slider-nav {
		transform: translate(12px, 19px);

		@include big-desktop {
			transform: translate(-12px, 19px);
		}

		@include small-tablet {
			transform: translate(-12px, 540px);
		}

		@include mobile {
			display: none;
		}
	}

	@include small-tablet {
		padding-top: 80px;
		padding-bottom: 60px;
	}
}

.history-nav {
	position: relative;
	grid-column: 4 span;

	&::before {
		content: '';
		position: absolute;
		left: 3px;
		top: 0;
		width: 1px;
		height: 100%;
		background-color: rgba(#9A9CA5, 0.5);
	}

	&__item {

		&:not(:last-child) {
			margin-bottom: 20px;
		}
	}

	&__btn {
		position: relative;
		padding-left: 40px;
		font-weight: 700;
		font-size: 28px;
		line-height: 150%;
		color: rgba(#9A9CA5, 0.5);
		transition: color 0.3s ease-in-out;

		&::before {
			content: '';
			position: absolute;
			left: 3px;
			top: 13px;
			width: 1px;
			height: 16px;
			background-color: var(--gray200-color);
		}

		&::after {
			content: '';
			position: absolute;
			left: 0px;
			top: 17px;
			border-radius: 100%;
			width: 8px;
			height: 8px;
			background-color: currentColor;
		}

		&:focus {
			outline: none;
			color: var(--orange-color);
		}

		&:hover {
			color: var(--orange-color);
		}

		&:active {
			color: var(--active-color);
		}

		&--active {
			color: var(--orange-color);
		}

		@include tablet {
			font-size: 24px;
		}
	}

	@include small-tablet {
		margin-bottom: 30px;
	}
}