.main-title {
	font-weight: 700;
	font-size: 72px;
	line-height: 130%;
	letter-spacing: 1px;
	text-transform: uppercase;
	color: var(--gray900-color);
	margin-bottom: 24px;

	@include small-tablet {
		font-size: 60px;
	}

	@include mobile {
		font-size: 35px;
	}
}

.main-descr {
	font-weight: 400;
	font-size: 20px;
	line-height: 150%;
	color: var(--gray700-color);

	@include mobile {
		font-size: 16px;
	}
}

.title {
	margin: 0;
	margin-bottom: 24px;
	font-weight: 700;
	font-size: 46px;
	line-height: 130%;
	color: var(--gray900-color);

	&--center {
		text-align: center;
	}

	@include tablet {
		font-size: 40px;
	}

	@include small-tablet {
		font-size: 35px;
	}
}

.footer-title {
	margin: 0;
	font-weight: 700;
	font-size: 16px;
	line-height: 150%;
	letter-spacing: 1px;
	text-transform: uppercase;
	color: var(--light-color);
}

.descr {
	margin: 0;
	margin-bottom: 60px;
	font-weight: 400;
	font-size: 18px;
	line-height: 150%;
	color: var(--gray700-color);

	&--center {
		text-align: center;
	}

	@include mobile {
		margin-bottom: 20px;
		font-size: 16px;
	}
}

.btn {
	display: inline-block;
	font-weight: 700;
	font-size: 16px;
	line-height: 20px;
	color: var(--light-color);
	text-align: center;
	letter-spacing: 0.5px;
	text-transform: uppercase;
	border-style: solid;
	border-width: 1px;
	border-color: transparent;
	border-radius: 4px;
	padding: 14px 38px;
	transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;

	&--fill {
		background-color: var(--orange-color);

		&:focus {
			outline: 2px solid var(--orange-color);
			outline-offset: 2px;
		}

		&:hover {
			background-color: var(--active-color);
		}

		&:active {
			background-color: var(--active-color);
		}

		@include mobile {
			padding: 14px 65px;
		}
	}

	&--stroke {

		&-light {
			border-color: var(--light-color);

			&:focus {
				outline: 2px solid var(--light-color);
				outline-offset: 2px;
			}

			&:hover {
				border-color: var(--orange-color);
				background-color: var(--orange-color);
			}

			&:active {
				background-color: var(--orange-color);
			}
		}

		&-orange {
			border: 1px solid #FF5A30;
			color: var(--orange-color);

			&:focus {
				outline: 2px solid var(--orange-color);
				outline-offset: 2px;
			}

			&:hover {
				background-color: var(--orange-color);
				color: var(--light-color);
			}

			&:active {
				background-color: var(--orange-color);
			}
		}
	}
}