.comments {
	padding-top: 80px;
	padding-bottom: 117px;

	&__title {
		margin: 0;
		margin-bottom: 60px;

		@include mobile {
			margin-bottom: 30px;
			font-size: 32px;
		}
	}

	&__list {}

	&__item {
		&:not(:last-child) {
			margin-bottom: 57px;

			@include mobile {
				margin-bottom: 40px;
			}
		}
	}

	@include tablet {
		padding-bottom: 60px;
	}


	@include mobile {
		padding-top: 60px;
	}
}

.comment {

	&__body {
		@include mobile {
			p {
				font-size: 14px;
			}
		}
	}

	&__left {
		width: 180px;

		@include mobile {
			margin-bottom: 10px;
		}
	}

	&__right {
		width: 100%;
	}

	&__author {
		font-size: 16px;
	}

	&__time {
		font-size: 12px;
	}

	@include mobile {
		flex-direction: column;
	}
}