.breadcrumbs {
	display: flex;

	&__item {
		position: relative;

		&:not(:last-child) {
			margin-right: 22px;

			&::after {
				content: "/";
				position: absolute;
				top: 1px;
				right: -14px;
				color: #9A9CA5;
			}

			@include small-tablet {
				margin-bottom: 10px;
			}
		}
	}

	&__link {
		font-weight: 400;
		font-size: 14px;
		line-height: 150%;
		color: var(--gray800-color);
		transition: color 0.3s ease-in-out;

		&:not([href]) {
			color: #9A9CA5;
		}

		&:focus {
			outline: 1px solid var(--orange-color);
			outline-offset: 2px;
		}

		&:hover {
			color: var(--orange-color);
		}

		&:active {
			color: var(--active-color);
		}
	}

	@include small-tablet {
		flex-wrap: wrap;
	}
}