.header-contacts {
	@include flex-v-center;

	&__link {
		display: flex;
		flex-direction: column;
		align-items: flex-start;

		&:not(:last-child) {
			margin-right: 37px;
		}

		@include tablet {
			display: none;
		}
	}

	&--nav {
		display: none;
	}

	@include tablet {
		display: block;
	}
}

.contacts-link {
	padding-left: 52px;
	background-position: left center;
	background-size: 40px 40px;
	background-repeat: no-repeat;

	&:focus {
		outline: 2px solid var(--orange-color);
		outline-offset: 2px;
	}

	&__caption {
		font-weight: 700;
		font-size: 14px;
		line-height: 150%;
		color: var(--gray800-color);
	}

	&__value {
		font-weight: 400;
		font-size: 18px;
		line-height: 150%;
		color: var(--gray900-color);
		transition: color 0.3s ease-in-out;

		@include mini-desktop {
			font-size: 16px;
		}

		&:hover {
			color: var(--orange-color);
		}

		&:active {
			color: var(--active-color);
		}
	}
}

.contacts-link--call {
	background-image: url("/img/header/iPhone.svg");
}

.contacts-link--mail {
	background-image: url("/img/header/Chat.svg");
}