.portfolio {
	position: relative;
	padding-bottom: 95px;

	@include mobile {
		padding-bottom: 60px;
	}
}

.portfolio-tabs-nav {
	display: flex;
	align-items: center;
	margin-bottom: 61px;
	transform: translateY(-60px);

	&__item {
		width: calc((100% - (20px * 4)) / 5);

		&:not(:last-child) {
			margin-right: 20px;
		}

		@include small-tablet {
			margin-bottom: 20px;
			width: calc((100% - (20px * 2)) / 3);
		}

		@include mobile {
			margin-right: 0 !important;
			width: 100%;
		}
	}

	&__btn {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		border-radius: 4px;
		padding: 15px;
		width: 100%;
		min-height: 154px;
		color: var(--orange-color);
		background-color: var(--light-color);
		box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
		transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

		&--active {
			color: var(--light-color);
			background-color: var(--orange-color);
			// pointer-events: none;

			.portfolio-tabs-nav__text {
				color: var(--light-color);
			}
		}
	}


	&__icon {
		display: block;
		margin-bottom: 17px;
		width: 48px;
		height: 48px;
		color: inherit;
		fill: currentColor;
		object-fit: cover;
		pointer-events: none;
	}

	&__text {
		font-weight: 700;
		font-size: 16px;
		line-height: 160%;
		text-align: center;
		color: #9A9CA5;
		transition: color 0.3s ease-in-out;
		pointer-events: none;
	}

	@include small-tablet {
		flex-wrap: wrap;
		justify-content: center;
		padding-top: 60px;
		transform: none;
	}
}

.portfolio-tabs-nav__btn {
	&:focus {
		outline: 2px solid var(--orange-color);
		outline-offset: 2px;
	}

	&:hover {
		.portfolio-tabs-nav__text {
			color: var(--gray900-color)
		}
	}
}

.portfolio-tabs-nav__btn--active {
	&:hover {
		background-color: var(--active-color);
		box-shadow: 0px 60px 80px -20px rgba(255, 90, 48, 0.16), 0px 26px 24px -10px rgba(255, 90, 48, 0.1), 0px 12px 10px -6px rgba(255, 90, 48, 0.08), 0px 4px 4px -4px rgba(30, 33, 44, 0.05);

		.portfolio-tabs-nav__text {
			color: var(--light-color);
		}
	}


}

.portfolio-tabs {
	@include grid;

	&__item {
		display: none;
		grid-column: 4 span;

		&--visible {
			display: block;
		}

		&--visible-more {
			display: block !important;
		}

		@include small-tablet {
			grid-column: 6 span;
		}
	}
}

.portfolio-more {
	display: inline-flex;
	align-items: center;
	margin-top: 60px;
	font-weight: 700;
	font-size: 16px;
	line-height: 160%;
	color: var(--gray800-color);
	transition: color 0.3s ease-in-out;

	svg {
		margin-right: 16px;
		width: 24px;
		height: 24px;
		color: inherit;
		fill: currentColor;
		object-fit: cover;
	}

	&:focus {
		outline: 2px solid var(--orange-color);
		outline-offset: 2px;
	}

	&:hover {
		color: var(--orange-color);
	}

	@include mobile {
		margin-top: 40px;
	}
}