.contacts-info {
	padding-top: 80px;
	padding-bottom: 120px;
	background-color: var(--gray200-color);

	&__list {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		--contacts-gap: 120px;
		gap: calc(var(--contacts-gap) * 2);
		margin-bottom: 124px;

		@include mini-desktop {
			--contacts-gap: 60px;
			margin-bottom: 80px;
		}

		@include tablet {
			--contacts-gap: 30px;
		}

		@include small-tablet {
			display: block;
		}
	}

	&__item {
		position: relative;

		&:not(:last-child) {
			&::after {
				content: '';
				position: absolute;
				top: 0;
				right: calc(-1 * var(--contacts-gap));
				width: 1px;
				height: 100%;
				background-image: url('/img/divider-contacts.svg');
				background-position: center;
				background-size: cover;
				background-repeat: no-repeat;
			}

			@include small-tablet {
				margin-bottom: 50px;
			}
		}
	}

	&__subtitle {
		margin: 0;
		margin-bottom: 24px;
		font-weight: 700;
		font-size: 28px;
		line-height: 150%;
		color: var(--gray900-color);

		@include tablet {
			font-size: 22px;
		}
	}

	&__address {
		display: inline-block;
		margin-bottom: 9px;
		font-style: normal;
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray900-color);

		@include tablet {
			font-size: 14px;
		}
	}

	&__link {
		display: inline-block;
		margin-bottom: 28px;
		border-bottom: 1px solid currentColor;
		font-weight: 400;
		font-size: 16px;
		color: var(--orange-color);
		transition: color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--orange-color);
			outline-offset: 2px;
		}

		&:hover {
			color: var(--active-color);
		}
	}

	&__element {
		color: var(--gray700-color);

		&:not(:last-child) {
			margin-bottom: 12px;
		}

		a {
			&:focus {
				outline: 2px solid var(--orange-color);
				outline-offset: 2px;
			}

			&:hover {
				color: var(--orange-color);
			}

			&:active {
				color: var(--active-color);
			}
		}
	}

	&__value {
		color: var(--gray900-color);
		transition: color 0.3s ease-in-out;

		@include tablet {
			font-size: 14px;
		}
	}

	&__soc {
		margin: 0;
		margin-bottom: 40px;
	}

	&__social {
		justify-content: center;

		.social__link {
			width: 32px;
			height: 32px;

			svg {
				color: var(--gray700-color);
				opacity: 1;

				&:hover {
					color: var(--orange-color);
				}

				&:active {
					color: var(--active-color);
				}
			}
		}

		.social__item {
			&:not(:last-child) {
				margin-right: 24px;
			}
		}
	}

	@include tablet {
		padding-bottom: 80px;
	}
}