.page-hero {
	padding-top: 152px;
	padding-bottom: 80px;
	min-height: 600px;
	background-repeat: no-repeat;
	background-color: var(--gray200-color);

	&__container {
		position: relative;
		z-index: 15;

	}

	&__breadcrumbs {
		margin-bottom: 40px;
	}

	&__content {}

	&__title {
		margin: 0;
		margin-bottom: 24px;
		font-weight: 700;
		font-size: 72px;
		line-height: 130%;
		letter-spacing: 1px;
		text-transform: uppercase;
		color: var(--gray900-color);


		&--small {
			margin-bottom: 60px;
			font-size: 64px;
			line-height: 120%;
			letter-spacing: 1px;
			text-transform: initial;
		}

		@include tablet {
			font-size: 55px;
		}

		@include mobile {
			font-size: 40px;
		}
	}

	&__descr {
		margin: 0;
		font-weight: 400;
		font-size: 20px;
		line-height: 150%;
		color: var(--gray700-color);
		max-width: 600px;

		@include mobile {
			font-size: 16px;
		}
	}

	&--services {
		background-image: url('/img/services_hero-bg.png');
		background-position: right -10px top;
	}

	&--services-inner {
		background-image: url('/img/services_hero-inner-bg.png');
		background-position: right -10px top;

		.page-hero__title {
			max-width: 600px;
		}
	}

	&--work {
		background-image: url('/img/work-hero-bg.png');
		background-position: right -10px top;
	}

	&--work-inner {
		background-color: var(--light-color);
		background-image: url('/img/work-hero-inner-bg.png');
		background-position: right -10px top 48px;
	}

	&--about {
		background-color: var(--gray200-color);
		background-image: url('/img/about-hero-bg.png');
		background-position: right -10px top 0px;
	}

	&--positions {
		background-color: var(--gray200-color);
		background-image: url('/img/hero-positions-bg.png');
		background-position: right -10px top 95px;

		.breadcrumbs__link {
			font-size: 13px;
		}

		.page-hero__title {
			max-width: 600px;

			@include tablet {
				font-size: 55px;
			}

			@include mobile {
				font-size: 38px;
			}
		}
	}

	&--news {
		background-color: var(--gray200-color);
		background-image: url('/img/hero-news-bg.png');
		background-position: right -10px top 0;
	}

	&--post {
		.page-hero__title {
			margin-bottom: 48px;
			font-size: 64px;
			line-height: 120%;
			text-transform: initial;

			@include tablet {
				font-size: 45px;
			}

			@include mobile {
				font-size: 26px;
			}
		}

		.page-hero__bottom {

			@include mobile {
				display: block;
			}
		}

		.page-hero__meta {
			@include mobile {
				margin-bottom: 30px;
			}

			.article-meta__item {
				&:not(:last-child) {
					margin-right: 22px;
				}
			}
		}
	}

	&--contacts {
		background-color: var(--gray200-color);
		background-image: url('/img/contacts-hero-bg.png');
		background-position: right -10px top 0;

		.page-hero__content {
			width: 600px;

			@include small-tablet {
				width: 100%;
			}
		}

		.page-hero__title {
			margin-bottom: 24px;
			font-size: 72px;
			line-height: 130%;
			text-transform: uppercase;

			@include small-tablet {
				font-size: 58px;
			}

			@include mobile {
				font-size: 50px;
			}
		}
	}

	@include tablet {
		min-height: auto;
		background-image: none;
	}

	@include mobile {
		padding-top: 90px;
	}
}