.testimonials {
	padding-top: 80px;
	background-color: var(--gray200-color);

	@include small-tablet {
		padding-top: 10px;
		padding-bottom: 60px;
	}

	&__container {
		position: relative;
		display: flex;
		justify-content: space-between;
	}

	&__content {
		max-width: 612px;

		@include tablet {
			max-width: 440px;
		}

		@include small-tablet {
			max-width: 100%;
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 60px;

		@include mobile {
			font-size: 30px;
		}
	}

	&__items {
		position: relative;
		padding-bottom: 13px;
	}

	&__slider-nav {
		position: absolute;
		right: 3px;
		bottom: 5px;
	}

	&__image {
		margin-bottom: -61px;
		z-index: 10;

		@include mini-desktop {
			width: 380px;
		}

		@include tablet {
			width: 300px;
		}

		@include small-tablet {
			display: none;
		}
	}
}

.testimonials-item {

	&__image {
		overflow: hidden;
		margin-bottom: 24px;
		border-radius: 100%;
		width: 72px;
		height: 72px;

		img {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__descr {
		margin: 0;
		margin-bottom: 25px;
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray800-color);
	}

	&__author {
		display: block;
		margin-bottom: 3px;
		max-width: 300px;
		font-weight: 700;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray900-color);
	}

	&__author-position {
		display: block;
		max-width: 300px;
		font-weight: 400;
		font-size: 14px;
		line-height: 150%;
		color: var(--gray700-color);
	}
}