.work-images {
	overflow: hidden;

	&__prev {
		position: absolute;
		top: 50%;
		left: 32px;
		z-index: 50;
		background-color: rgba(#fff, 0.5);
		transform: translateY(-50%);

		@include mobile {
			display: none;
		}
	}

	&__next {
		position: absolute;
		top: 50%;
		right: 32px;
		z-index: 50;
		background-color: rgba(#fff, 0.5);
		transform: translateY(-50%);

		@include mobile {
			display: none;
		}
	}

	&-slider {
		position: relative;
		margin-bottom: 20px;
	}

	.swiper-slide img {
		display: block;
		border-radius: 4px;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.work-images-nav {
	overflow: hidden;

	.swiper-slide {
		border-radius: 4px;
		opacity: 0.6;
		border: 1px solid transparent;
		transition: opacity 0.3s ease-in-out;
	}

	.swiper-slide img {
		display: block;
		border-radius: 4px;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}