.partners {
	padding-top: 127px;
	padding-bottom: 118px;
	background-color: var(--gray200-color);

	@include small-tablet {
		padding-top: 60px;
		padding-bottom: 60px;
	}

	&--part {
		padding-top: 120px;
		padding-bottom: 108px;

		.partners__title {
			margin-bottom: 24px;
		}

		@include small-tablet {
			padding-top: 60px;
			padding-bottom: 0px;
		}
	}

	&__container {}

	&__title {
		margin-bottom: 60px;

		@include mobile {
			font-size: 32px;
		}
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(6, 1fr);
		column-gap: 102px;
		row-gap: 35px;

		@include mini-desktop {
			column-gap: 30px;
		}
	}

	&__item {
		@include mini-desktop {
			display: flex;
			justify-content: center;
			grid-column: 2 span;
		}

		@include mobile {
			grid-column: 3 span;
		}
	}

	&__link {
		&:focus {
			outline: 2px solid var(--orange-color);
			outline-offset: 2px;
		}
	}

	&__logo {}
}