.pricing {
	position: relative;
	padding-top: 83px;
	background-color: #F4F5F7;
	overflow: hidden;

	&::before {
		content: '';
		position: absolute;
		left: 100px;
		top: -40px;
		width: 570px;
		height: 1065px;
		background-image: url(/img/services-pricing-bg.png);
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
	}

	&__wrapper {
		position: relative;
		z-index: 10;
		border: 1px solid #E5E8ED;
		box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
		border-radius: 4px;
		background-color: var(--light-color);

		@include small-tablet {
			overflow-x: auto;
		}
	}

	&__table {}
}

.table {
	border-collapse: collapse;
	width: 100%;

	&__th {
		padding: 32px 0;
		padding-right: 10px;

		&:first-child {
			padding-left: 24px;
			padding-bottom: 32px;
			vertical-align: bottom;
			font-weight: 700;
			font-size: 18px;
			line-height: 150%;
			color: var(--gray900-color);
			text-align: left;
		}
	}

	&__heading {
		margin-bottom: 3px;
		font-weight: 700;
		font-size: 24px;
		line-height: 150%;
		color: var(--gray900-color);
		text-align: center;
		text-transform: uppercase;
	}

	&__per {
		font-weight: 700;
		font-size: 18px;
		line-height: 150%;
		text-align: center;
		color: var(--gray700-color);
	}

	&__body {
		.table__row {
			&:nth-child(odd) {
				background: #F4F5F7;
			}

			&:last-child {
				.table__column {
					padding: 32px 0;
				}
			}
		}
	}

	&__column {
		padding: 15px 0;
		padding-right: 5px;
		vertical-align: middle;
		text-align: center;
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray800-color);

		&:first-child {
			padding-left: 24px;
			text-align: left;
		}
	}

	&__check {
		display: inline-block;
		vertical-align: middle;
		font-size: 0;
		width: 24px;
		height: 24px;
		background-image: url('/img/table-check.svg');

		@include bg-cover;
	}

	&__text {
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray800-color);
	}

	&__btn {
		padding: 7px 23px;
		font-weight: 700;
		font-size: 12px;
		letter-spacing: 0.5px;
		text-transform: uppercase;
		color: var(--orange-color);

		@include tablet {
			padding: 10px;
		}
	}

	@include small-tablet {
		width: 1200px;
	}
}