.video-block {
	position: relative;
	border-radius: 4px;
	height: 500px;
	overflow: hidden;

	@include mobile {
		height: 300px;
	}

	&::before {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		z-index: 3;
		background-color: rgba(#1E212C, 0.6);
		transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
	}

	&--played {
		&::before {
			opacity: 0;
			visibility: hidden;
		}
	}

	&__play {
		@include flex-all-center;

		position: absolute;
		left: 50%;
		top: 50%;
		width: 80px;
		height: 80px;
		border-radius: 100%;
		z-index: 50;
		background-color: var(--orange-color);
		transform: translate(-50%, -50%);
		transition: background-color 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

		svg {
			width: 26px;
			height: 26px;
			fill: var(--light-color);
		}

		&:focus {
			outline: 2px solid var(--orange-color);
			outline-offset: 2px;
		}

		&:hover {
			background-color: var(--active-color);
		}

		&:active {
			background-color: var(--active-color);
		}

		&--played {
			opacity: 0;
			visibility: inherit;
		}
	}

	&__content {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}