.team {
	padding-top: 70px;
	padding-bottom: 120px;

	&__list {
		@include grid;
		row-gap: 60px;
		margin-bottom: 80px;

		@include small-tablet {
			row-gap: 30px;
			column-gap: 20px;
			margin-bottom: 40px;
		}

	}

	&__item {
		grid-column: 3 span;

		&:hover {
			.team__social {
				opacity: 1;
				visibility: visible;
			}
		}

		@include small-tablet {
			grid-column: 6 span;
		}

		@include mobile {
			grid-column: 4 span;
		}
	}

	&__top {
		position: relative;
		margin-bottom: 15px;
		font-size: 0;
		width: 285px;
		height: 340px;

		@include mini-desktop {
			width: auto;
			height: auto;
		}

		@include mobile {
			display: flex;
			justify-content: center;
		}
	}

	&__image {
		width: 100%;
		height: 100%;

		@include mobile {
			width: 80%;
		}
	}

	&__name {
		margin-bottom: 5px;
		font-weight: 700;
		font-size: 20px;
		line-height: 150%;
		text-align: center;
		color: var(--gray800-color);

		@include small-tablet {
			font-size: 16px;
		}
	}

	&__post {
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		text-align: center;
		color: var(--gray700-color);

		@include small-tablet {
			font-size: 14px;
		}
	}

	&__social {
		position: absolute;
		left: 0;
		bottom: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 20px 20px;
		border-radius: 0px 0px 4px 4px;
		width: 100%;
		background-color: rgba(#FF5A30, 0.7);
		opacity: 0;
		visibility: hidden;
		transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
	}

	&__link {
		display: block;
		width: 20px;
		height: 20px;
		fill: var(--light-color);

		svg {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&:not(:last-child) {
			margin-right: 15px;
		}
	}

	&__text {
		font-weight: 400;
		font-size: 18px;
		line-height: 150%;
		text-align: center;
		color: var(--gray900-color);

		a {
			border-bottom: 1px solid currentColor;
			color: var(--orange-color);
			transition: color 0.3s ease-in-out;

			&:focus {
				outline: 2px solid var(--orange-color);
				outline-offset: 2px;
			}

			&:hover {
				color: var(--active-color);
			}

			&:active {
				color: var(--active-color);
			}
		}
	}

	@include small-tablet {
		padding-top: 60px;
		padding-bottom: 60px;
	}
}