.learn-more {
	@include flex-all-center;
	position: relative;
	z-index: 15;

	&__subtitle {
		margin: 0;
		margin-right: 40px;
		font-weight: 700;
		font-size: 28px;
		line-height: 150%;
		color: var(--gray900-color);

		@include small-tablet {
			margin-right: 20px;
			max-width: 50%;
			font-size: 26px;
		}

		@include mobile {
			margin-bottom: 50px;
			text-align: center;
			max-width: 100%;
		}
	}

	@include mobile {
		flex-wrap: wrap;
	}

}