.facts {
	position: relative;
	overflow: hidden;
	padding-top: 181px;
	padding-bottom: 180px;
	background-color: var(--light-color);

	@include small-tablet {
		padding-top: 60px;
		padding-bottom: 60px;
	}


	&::before {
		content: '';
		position: absolute;
		left: -8px;
		top: -196px;
		width: 570px;
		height: 1065px;
		background-image: url(/img/facts/facts-bg.png);
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
	}

	&__container {}

	&__title {
		margin: 0;
		margin-bottom: 61px;
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: 48px;
		margin: 0 auto;
		max-width: 736px;

		@include small-tablet {
			grid-template-columns: repeat(2, 1fr);
		}

		@include mobile {
			grid-template-columns: repeat(1, 1fr);
		}
	}

	&__item {}
}

.facts-element {
	--progress-stroke: #143080;
	--progress-bg-opacity: 0.3;

	&__circle {
		position: relative;
		width: 148px;
		height: 148px;
		margin-bottom: 19px;

		svg {
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
		}

		.progress {
			stroke: var(--progress-stroke);
			transform: rotate(-90deg) translateX(21px);
			transform-origin: center;
		}

		.progress-bg {
			stroke: var(--progress-stroke);
			opacity: var(--progress-bg-opacity);
		}

		&--one-color {
			--progress-stroke: #F89828;
		}

		&--two-color {
			--progress-stroke: #F52F6E;
		}

		&--three-color {
			--progress-stroke: #5A87FC;
		}

		&--four-color {
			--progress-stroke: #03CEA4;
		}
	}

	&__value {
		position: absolute;
		top: 50%;
		left: 50%;
		font-weight: 700;
		font-size: 32px;
		line-height: 150%;
		color: var(--gray900-color);
		transform: translate(-50%, -50%);

		@include mobile {
			font-size: 28px;
		}
	}

	&__text {
		display: block;
		font-weight: 400;
		font-size: 14px;
		line-height: 150%;
		text-align: center;
		color: var(--gray900-color);

	}

	@include small-tablet {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}