.contacts {
	padding-top: 120px;
	padding-bottom: 120px;

	&__content {
		display: flex;
		justify-content: space-between;

		@include tablet {
			flex-wrap: wrap;
			justify-content: center;
		}
	}

	&__image {
		flex-shrink: 0;
		margin-right: 20px;

		@include tablet {
			margin-right: 0;
			margin-bottom: 40px;
		}
	}

	&__form {}

	.form-contacts {
		width: 600px;

		&__line {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			margin-bottom: 23px;
			width: 100%;

			@include mobile {
				display: block;
			}
		}

		&__field {
			width: 48%;

			&--big {
				width: 100%;
			}

			@include mobile {
				width: 100%;

				&:not(:last-child) {
					margin-bottom: 20px;
				}
			}
		}

		&__radio {
			&:not(:last-child) {
				margin-right: 24px;
			}
		}

		&__fields {
			padding: 0;
			border: none;
			width: 48%;

			.form-field__caption {
				margin-bottom: 24px;
			}
		}

		&__bottom {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding-top: 40px;

			@include mobile {
				flex-direction: column;
				align-items: center;
			}
		}

		&__checkbox {
			max-width: 315px;

			.custom-checkbox__text {
				font-weight: 400;
				font-size: 16px;
				line-height: 160%;
				color: var(--gray800-color);

				&::before {
					top: 4px;
				}

				&::after {
					top: 4px;
				}

				@include mobile {
					margin-bottom: 30px;
				}
			}
		}

		&__btn {
			padding: 14px 41px;
		}

		@include tablet {
			width: 100%;
		}
	}

	.form-field {

		&__flex {
			padding-left: 4px;
		}

		&__caption {
			font-size: 16px;
			line-height: 160%;
			color: var(--gray800-color);

		}

		&__input {
			height: 52px;
			font-size: 16px;
			line-height: 160%;
			color: var(--gray700-color);
		}

		.choices {
			width: 100%;
		}

		.is-focused {
			outline: 2px solid var(--orange-color);
			outline-offset: 2px;
		}

		.choices__inner {
			padding: 0;
			margin: 0;
			padding: 13px 16px;
			border-radius: 4px;
			border: 1px solid #D7DADD;
			height: 52px;
			background: #F4F5F7;
		}

		.choices__list--single .choices__placeholder {
			font-size: 16px;
			color: var(--gray700-color);
			opacity: 1;
		}

		.choices[data-type*=select-one]:after {
			border: none;
			right: 18px;
			width: 14px;
			height: 7px;
			background-image: url('/img/angle-down.svg');
			background-position: center;
			background-size: cover;
			background-repeat: no-repeat;
			transform: translateY(-50%);
			transition: transform 0.3s ease-in-out;
		}

		.choices[data-type*=select-one].is-open:after {
			transform: rotate(180deg) translateY(-50%);
		}

		.choices__list--dropdown .choices__item--selectable:after {
			display: none;
		}

		.choices__list--dropdown {
			background-color: var(--light-color);
		}
	}

	@include tablet {
		padding-top: 80px;
		padding-bottom: 80px;
	}
}

.form-contacts .form-field__input--big {
	height: 127px;
}