.services {
	position: relative;
	overflow: hidden;
	padding-top: 120px;
	padding-bottom: 176px;
	background-color: var(--light-color);

	&__decorate-left {
		position: absolute;
		left: -10px;
		bottom: -247px;
	}

	&__decorate-right {
		position: absolute;
		right: -10px;
		bottom: 800px;
	}

	&__container {}

	&__list {
		position: relative;
		z-index: 5;
	}

	&__item {
		display: grid;
		grid-template-columns: repeat(12, 1fr);
		column-gap: 80px;
		align-items: center;

		&:not(:last-child) {
			margin-bottom: 117px;

			@include tablet {
				margin-bottom: 60px;
			}
		}

		&--reverse {
			.services__info {
				order: -1;

				@include mobile {
					order: initial;
				}
			}
		}

		@include tablet {
			grid-template-columns: repeat(10, 1fr);
			column-gap: 40px;
		}

		@include mobile {
			grid-template-columns: repeat(6, 1fr);
		}
	}

	&__image {
		grid-column: 6 span;

		@include tablet {
			grid-column: 5 span;
		}

		@include mobile {
			grid-column: 6 span;
			margin-bottom: 20px;
		}
	}

	&__info {
		grid-column: 6 span;

		@include tablet {
			grid-column: 5 span;
		}

		@include mobile {
			grid-column: 6 span;
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 24px;
		font-weight: 700;
		font-size: 32px;
		line-height: 150%;
		color: var(--gray900-color);

		@include mobile {
			font-size: 28px;
		}
	}

	&__descr {
		margin: 0;
		margin-bottom: 48px;
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray700-color);

		@include mobile {
			margin-bottom: 30px;
			font-size: 14px;
		}
	}

	&__link {
		padding: 12px 31px;
		font-weight: 700;
		font-size: 14px;
		letter-spacing: 0.5px;
		text-transform: uppercase;
	}

	@include mobile {
		padding-top: 60px;
		padding-bottom: 80px;
	}
}