.positions-content {
	padding-top: 120px;
	padding-bottom: 180px;
	background-color: var(--light-color);

	&__container {}

	&__info {
		@include grid;


	}

	&__left {
		grid-column: 7 span;
	}

	&__right {
		display: flex;
		justify-content: flex-end;
		grid-column: 5 span;

		@include tablet {
			padding-bottom: 55px;
		}

		@include small-tablet {
			padding-bottom: 100px;
		}

		@include mobile {
			justify-content: flex-start;
			padding-bottom: 10px;
		}
	}

	@include tablet {
		padding-top: 80px;
		padding-bottom: 80px;
	}
}

.positions-list {

	&__item {
		&:not(:last-child) {
			margin-bottom: 23px;
		}
	}
}

.positions-item {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 32px 39px;
	border: 1px solid #E5E8ED;
	border-radius: 4px;
	background-color: var(--light-color);
	transition: border 0.3s ease-in-out, box-shadow 0.3s ease-in-out;

	&:hover {
		border: 1px solid #E5E8ED;
		box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
	}

	&__left {

		@include tablet {
			margin-bottom: 30px;
		}
	}

	&__meta {
		margin-bottom: 8px;
		font-weight: 400;
		font-size: 14px;
		line-height: 150%;
		color: var(--gray700-color);

		@include mobile {
			font-size: 13px;
		}
	}

	&__city {
		position: relative;
		padding-left: 23px;
		margin-right: 22px;
		background-image: url('/img/city.svg');
		background-position: left center;
		background-size: 16px 16px;
		background-repeat: no-repeat;

		&::before {
			content: '';
			position: absolute;
			right: -13px;
			top: 50%;
			width: 1px;
			height: 11px;
			background-color: currentColor;
			transform: translateY(-50%);
		}
	}

	&__time {
		padding-left: 23px;
		background-image: url('/img/time.svg');
		background-position: left center;
		background-size: 16px 16px;
		background-repeat: no-repeat;
	}

	&__title {
		margin: 0;
		font-weight: 700;
		font-size: 20px;
		line-height: 150%;
		color: var(--gray900-color);

	}

	&__link {
		font-size: 14px;
		padding: 11px 31px;
	}

	@include tablet {
		flex-direction: column;
		align-items: flex-start;
	}
}

.positions-looking {
	position: sticky;
	top: 0px;
	width: 390px;
	height: 300px;
	transition: box-shadow 0.3s ease-in-out;

	&:hover {
		box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
	}

	&__title {
		margin: 0;
		margin-bottom: 24px;
		font-weight: 700;
		font-size: 28px;
		line-height: 150%;
		color: var(--gray900-color);

		@include small-tablet {
			font-size: 24px;
		}
	}

	&__descr {
		margin: 0;
		margin-bottom: 48px;
		font-weight: 400;
		font-size: 18px;
		line-height: 150%;
		color: var(--gray800-color);

		@include small-tablet {
			font-size: 16px;
		}
	}

	&__btns {

		@include tablet {
			display: flex;
			flex-direction: column;
			align-items: center;
		}
	}

	&__btn {
		padding: 16px 43px;

		&:last-child {
			padding: 16px 50px;

			@include tablet {
				padding: 11px;
				width: 70%;
			}
		}

		&:not(:last-child) {
			margin-right: 30px;

			@include tablet {
				margin-right: 0;
				margin-bottom: 20px;
				padding: 11px;
				width: 70%;
			}
		}
	}
}