.employee-benefits {
	padding-top: 0;
	padding-bottom: 180px;
	background-color: var(--light-color);

	&__descr {
		@include mobile {
			margin-bottom: 40px;
		}
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		--mission-gap: 94px;
		gap: calc(var(--mission-gap) * 2);

		@include tablet {
			--mission-gap: 30px;
		}

		@include small-tablet {
			--mission-gap: 20px;
			margin-bottom: 0;
		}

		@include mobile {
			display: block;
		}
	}

	&__item {
		position: relative;
		padding-top: 72px;
		text-align: center;
		background-position: top center;
		background-size: 48px 48px;
		background-repeat: no-repeat;

		&:not(:last-child) {
			&::after {
				content: '';
				position: absolute;
				top: 0;
				right: calc(-1 * var(--mission-gap));
				width: 1px;
				height: 100%;
				background-image: url('/img/mission/divider.svg');
				background-position: center;
				background-size: cover;
				background-repeat: no-repeat;
			}
		}

		&--training {
			background-image: url('/img/mission/train.svg');

		}

		&--growth {
			background-image: url('/img/mission/growth.svg');
		}

		&--salary {
			background-image: url('/img/mission/salary.svg');
		}

		@include mobile {
			&:not(:last-child) {
				margin-bottom: 30px;
			}
		}
	}

	&__subtitle {
		margin: 0;
		margin-bottom: 10px;
		font-weight: 700;
		font-size: 20px;
		line-height: 150%;
		color: var(--gray900-color);
	}

	&__descr-list {
		margin: 0;
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray700-color);

		@include small-tablet {
			font-size: 14px;
		}
	}

	@include tablet {
		padding-bottom: 60px;
	}

	@include mobile {
		padding-top: 60px;
	}
}