.project-goal {
	padding-top: 15px;
	padding-bottom: 120px;

	&__container {
		@include grid;

		@include tablet {
			grid-template-columns: repeat(6, 1fr);
		}
	}

	&__left {
		padding-top: 39px;
		grid-column: 6 span;

		@include small-tablet {
			padding-top: 10px;
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 40px;
		font-weight: 700;
		font-size: 46px;
		line-height: 130%;
		color: var(--gray900-color);

		@include mobile {
			font-size: 40px;
		}
	}

	&__descr {
		margin: 0;
		font-weight: 400;
		font-size: 18px;
		line-height: 150%;
		color: var(--gray800-color);

		&:not(:last-child) {
			margin-bottom: 25px;
		}

		@include mobile {
			font-size: 16px;
		}
	}

	&__right {
		display: flex;
		justify-content: flex-end;
		grid-column: 6 span;

		@include tablet {
			justify-content: flex-start;
		}
	}

	@include small-tablet {
		padding-bottom: 80px;
	}
}

.project-info {
	padding: 40px 60px;
	box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
	border-radius: 4px;
	width: 495px;
	min-height: 316px;
	background-color: var(--light-color);

	&__list {}

	&__item {
		display: flex;

		&:not(:last-child) {
			margin-bottom: 16px;
		}
	}

	&__category {
		flex-shrink: 0;
		width: 130px;
		font-weight: 700;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray900-color);
		text-transform: uppercase;

		@include tablet {
			font-size: 14px;
		}
	}

	&__value {
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray700-color);

		@include tablet {
			font-size: 14px;
		}
	}

	@include tablet {
		padding: 20px;
	}
}