.news-article {
	overflow: hidden;
	border-radius: 4px;
	height: 100%;
	box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
	background-color: var(--light-color);

	&--small {

		.news-article__text {
			padding: 12px 24px;
		}

		.news-article__title {
			margin-bottom: 9px;
		}

		.news-article__descr {
			display: none;
		}
	}

	&__image {
		@include tablet {
			width: 100%;
			object-fit: cover;
		}
	}

	&__text {
		padding: 20px 24px;
	}

	&__title {
		margin: 0;
		margin-bottom: 13px;
	}

	&__link {
		font-weight: 700;
		font-size: 20px;
		line-height: 150%;
		color: var(--gray900-color);
		transition: color 0.3s ease-in-out;

		&:focus {
			outline: 1px solid var(--orange-color);
			outline-offset: 2px;
		}

		&:hover {
			color: var(--orange-color);
		}

		&:active {
			color: var(--active-color);
		}
	}

	&__meta {
		margin-bottom: 16px;
	}

	&__descr {
		margin: 0;
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray800-color);

	}
}

.article-meta {
	font-weight: 400;
	line-height: 150%;
	font-size: 14px;
	color: var(--gray700-color);

	&__item {
		position: relative;
		color: inherit;
		transition: color 0.3s ease-in-out;

		&:not(:last-child) {
			margin-right: 20px;

			&::after {
				content: '';
				position: absolute;
				right: -15px;
				top: 0;
				width: 1px;
				height: 12px;
				color: inherit;
				background-color: currentColor;
			}
		}
	}

	&__item--comments {
		padding-left: 26px;
		background-image: url('/img/recent-article/comments.svg');
		background-position: left center;
		background-size: 16px 16px;
		background-repeat: no-repeat;
	}
}

a.article-meta__item {
	&:focus {
		outline: 1px solid var(--orange-color);
		outline-offset: 2px;
	}

	&:hover {
		color: var(--orange-color);
	}

	&:active {
		color: var(--active-color);
	}
}