.hero {
	height: 100%;

	&__container {
		height: 100%;
	}

	&__content {
		max-width: 590px;
	}

	&__next {
		position: absolute;
		right: 32px;
		top: 50%;
		z-index: 10;
		background-color: rgba(255, 255, 255, 0.4);
		transform: translateY(-50%);

		@include big-desktop {
			display: none;
		}
	}

	&__prev {
		position: absolute;
		left: 32px;
		top: 50%;
		z-index: 10;
		background-color: rgba(255, 255, 255, 0.4);
		transform: translateY(-50%);

		@include big-desktop {
			display: none;
		}
	}


	&__main-title {
		margin: 0;
		margin-bottom: 24px;
		color: var(--light-color);

		span {
			color: var(--orange-color);
		}
	}

	&__main-descr {
		margin: 0;
		margin-bottom: 61px;
		color: var(--light-color);
	}

	&__btns {
		display: flex;

		@include mobile {
			display: inline;
		}

		&>:not(:last-child) {
			margin-right: 25px;

			@include mobile {
				margin-bottom: 20px;
			}

			@include mini-mobile {
				margin-right: 0;
			}
		}
	}

	&__pag {
		display: flex;
		counter-reset: pag;
		left: calc((100% - 1260px) / 2) !important;
		bottom: 120px !important;

		.swiper-pagination-bullet {
			display: flex;
			flex-direction: column;
			border-radius: 0;
			width: 178px;
			height: auto;
			text-align: left;
			background-color: transparent;
			opacity: 1;

			&::before {
				counter-increment: pag;
				content: "0" counter(pag);
				font-weight: 700;
				font-size: 28px;
				line-height: 150%;
				color: rgba(#fff, 0.5);
				transition: color 0.3s ease-in-out;
			}

			&::after {
				display: none;
			}

			@include small-tablet {
				width: 25%;
			}
		}

		@include big-desktop {
			left: 3% !important;
		}

		@include mini-desktop {
			left: 15px !important;
		}

		@include small-tablet {
			bottom: 60px !important;
			width: calc(100% - 30px) !important;
		}

		@include mobile {
			display: none;
		}
	}

	&__bar {
		position: relative;
		overflow: hidden;
		border-radius: 100px;
		width: 100%;
		height: 2px;
		background-color: rgba(#fff, 0.3);

		&::after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background-color: var(--light-color);
			transform: scaleX(0);
			transform-origin: left;
		}
	}
}

.swiper-slide {
	height: auto;

}

.hero-slide {
	height: 100%;
}

.swiper-pagination-bullet-active {

	&::before {
		color: var(--light-color) !important;
	}

	.hero__bar {
		&::after {
			transform: scaleX(1);
			transition: transform var(--hero-slider-speed) ease-in-out;
		}
	}
}

.hero-slider {
	position: relative;
	overflow: hidden;
	width: 100%;

	.swiper-button-disabled {
		background-color: var(--light-color);
		color: var(--gray800-color);
		cursor: auto;
	}
}

.hero-slide {
	padding-top: 221px;
	padding-bottom: 355px;
	background-image: url("/img/main-hero/hero-bg.jpg");
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	background-color: var(--gray700-color);

	@include small-tablet {
		padding-top: 150px;
		padding-bottom: 200px;
	}

	@include mobile {
		padding-bottom: 100px;
	}
}