.social {
	display: flex;
	align-items: center;

	&__item {
		&:not(:last-child) {
			margin-right: 16px;
		}
	}

	&__link {
		display: block;
		width: 24px;
		height: 24px;
		color: rgba(255, 255, 255, 0.6);

		&:focus {
			outline: 1px solid var(--orange-color);
			outline-offset: 2px;
		}

		svg {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
			fill: currentColor;
			transition: color 0.3s ease-in-out;

			&:hover {
				color: var(--orange-color);
			}

			&:active {
				color: var(--active-color);
			}
		}
	}
}