.services-section {
	padding-top: 210px;
	padding-bottom: 80px;
	background-image: url('/img/services-section/services-bg.jpg');
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	background-color: #fff;

	@include small-tablet {
		padding-top: 180px;
	}

	&__list {
		margin-bottom: 83px;
	}
}

.services-list {
	display: grid;
	grid-template-columns: repeat(12, 1fr);
	column-gap: 30px;
	row-gap: 30px;

	&__item {
		grid-column: 3 span;

		@include tablet {
			grid-column: 6 span;
		}

		@include mobile {
			grid-column: 4 span;
		}
	}

	@include mobile {
		grid-template-columns: repeat(4, 1fr);
	}
}

.services-item {
	position: relative;
	display: block;
	padding: 88px 10px;
	border: 1px solid #E5E8ED;
	border-radius: 4px;
	box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
	background: var(--light-color);

	&:focus {
		outline: 2px solid var(--orange-color);
		outline-offset: 2px;
	}

	&::before {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		z-index: 3;
		border-radius: inherit;
		background-color: rgba(#1E212C, 0.6);
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
		opacity: 0;
		transition: opacity 0.3s ease-in-out;
	}

	&::after {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		z-index: 5;
		border-radius: inherit;
		background-color: rgba(#1E212C, 0.6);
		opacity: 0;
		transition: opacity 0.3s ease-in-out;
	}

	&__content {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		z-index: 10;
	}

	&__icon {
		width: 52px;
		height: 52px;
		margin-bottom: 24px;
		object-fit: cover;
		fill: var(--orange-color);
		transition: fill 0.3s ease-in-out;
	}

	&__title {
		margin: 0;
		font-weight: 700;
		font-size: 20px;
		line-height: 150%;
		text-align: center;
		color: var(--gray900-color);
		transition: color 0.3s ease-in-out;

		@include mini-desktop {
			font-size: 18px;
		}
	}

	&--construct {
		&::before {
			background-image: url('/img/services-section/interior.jpg');
		}
	}

	&:hover {
		&::before {
			opacity: 1;
		}

		&::after {
			opacity: 1;
		}

		.services-item__icon {
			fill: var(--light-color);
		}

		.services-item__title {
			color: var(--light-color);
		}
	}
}