.categories {
	padding-top: 120px;
	padding-bottom: 180px;

	&__container {}

	&__title {
		margin: 0;
		margin-bottom: 40px;
	}

	@include mobile {
		padding-top: 60px;
		padding-bottom: 80px;
	}
}

.categories-nav {
	display: flex;
	align-items: center;
	margin-bottom: 60px;

	&__item {
		width: calc(100% / 6);

		@include tablet {
			display: flex;
			justify-content: center;
			width: calc(100% / 3);
			margin-bottom: 20px;
		}

		@include mobile {
			width: calc(100% / 2);
		}
	}

	&__btn {
		@include flex-all-center;

		padding: 10px;
		border: 1px solid transparent;
		border-radius: 4px;
		width: 100%;
		font-weight: 700;
		font-size: 16px;
		line-height: 160%;
		text-align: center;
		color: var(--gray700-color);
		transition: color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--orange-color);
			outline-offset: 2px;
		}

		&:hover {
			color: var(--orange-color);
		}

		&:active {
			color: var(--active-color);
		}

		&--active {
			border-color: currentColor;
			color: var(--orange-color)
		}

		@include tablet {
			width: 90%;
		}

		@include mobile {
			width: 90%;
			font-size: 14px;
		}
	}

	@include tablet {
		flex-wrap: wrap;
	}
}

.categories-list {
	@include grid;

	row-gap: 60px;
	margin-bottom: 60px;

	&__item {
		grid-column: 6 span;
	}

	@include mobile {
		margin-bottom: 40px;
	}
}

.news-article {

	&__descr {
		overflow: hidden;
		height: 52px;
	}
}

.categories-pag {
	@include flex-all-center;

	&__item {
		&:not(:last-child) {
			margin-right: 20px;
		}
	}

	&__link {
		font-weight: 700;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray800-color);
		transition: color 0.3s ease-in-out;

		svg {
			display: block;
			width: 24px;
			height: 24px;
			fill: currentColor;
			object-fit: cover;
		}

		&:focus {
			outline: 2px solid var(--orange-color);
			outline-offset: 2px;
		}

		&:hover {
			color: var(--orange-color);
		}

		&:active {
			color: var(--active-color);
		}

		&--active {
			color: var(--orange-color);
		}

	}
}