.achieve {
	padding-top: 117px;
	padding-bottom: 120px;
	background-image: url('/img/achieve/achieve-bg.png');
	background-position: right -10px bottom -523px;
	background-repeat: no-repeat;
	background-color: var(--light-color);
	overflow: hidden;

	&__container {}

	&__list {
		margin-bottom: 239px;

		@include grid;
		column-gap: 70px;

		@include small-tablet {
			grid-template-columns: repeat(8, 1fr);
			margin-bottom: 100px;
		}

		@include mobile {
			grid-template-columns: repeat(4, 1fr);
		}
	}

	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		grid-column: 3 span;
		padding-top: 70px;
		background-position: top center;
		background-size: 48px 48px;
		background-repeat: no-repeat;

		&--reccom {
			background-image: url('/img/achieve/01.svg');
		}

		&--renov {
			background-image: url('/img/achieve/02.svg');
		}

		&--spec {
			background-image: url('/img/achieve/03.svg');
		}

		&--finish {
			background-image: url('/img/achieve/04.svg');
		}

		@include tablet {
			grid-column: 6 span;
		}

		@include small-tablet {
			grid-column: 4 span;
		}
	}


	&__value {
		margin-bottom: 8px;
		font-weight: 700;
		font-size: 46px;
		line-height: 130%;
		color: var(--orange-color);

		@include big-desktop {
			font-size: 43px;
		}

		@include mini-desktop {
			font-size: 32px;
		}
	}

	&__text {
		font-weight: 400;
		font-size: 14px;
		line-height: 150%;
		max-width: 240px;
		text-align: center;
		color: var(--gray900-color);

	}

	@include small-tablet {
		padding-top: 60px;
		padding-bottom: 60px;
	}
}

.achieve {

	&__content {
		display: flex;
		align-items: flex-start;

		@include tablet {
			display: block;
		}
	}

	&__image {

		@include mini-desktop {
			max-width: 45%;
		}

		@include tablet {
			max-width: 100%;
		}
	}

	&__info {
		position: relative;
		left: -30px;
		top: -120px;
		z-index: 10;
		flex-shrink: 0;
		border-radius: 4px;
		padding-left: 135px;
		padding-top: 80px;
		padding-right: 15px;
		padding-bottom: 80px;
		width: 735px;
		box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
		background-color: var(--gray200-color);

		@include mini-desktop {
			max-width: 55%;
			left: 0;
			top: -50px;
		}

		@include tablet {
			max-width: 100%;
		}

		@include mobile {
			padding: 15px;
		}
	}
}

.quote {
	margin: 0;

	&__content {
		position: relative;
		margin-bottom: 60px;

		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 28px;
			height: 20px;
			background-image: url('/img/achieve/braces.svg');
			transform: translateX(-58px);

			@include bg-cover;

			@include mobile {
				left: 50px;
				width: 24px;
			}
		}

		@include mobile {
			padding-top: 40px;
		}
	}

	&__descr {
		margin: 0;
		max-width: 495px;
		font-weight: 700;
		font-size: 20px;
		line-height: 150%;
		color: var(--gray900-color);

		&:not(:last-child) {
			margin-bottom: 30px;
		}

		@include mobile {
			font-size: 18px;
		}
	}

	&__footer {
		position: relative;
		font-weight: 400;
		font-size: 14px;
		line-height: 150%;
		color: var(--gray700-color);

		&::after {
			content: '';
			position: absolute;
			right: 0;
			bottom: 0;
			width: 100px;
			height: 68px;
			background-image: url('/img/achieve/signature.svg');
			transform: translate(-105px, 8px);

			@include bg-cover;

			@include mini-desktop {
				transform: translate(-35px, 8px);
			}

			@include mobile {
				transform: translate(1px, -18px);
			}
		}
	}

	&__author {
		display: block;
		margin-bottom: 5px;
		font-weight: 700;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray900-color);
	}
}