.comment {
	display: flex;

	&__left {
		padding-right: 30px;
		width: calc(100% - 630px);
	}

	&__author {
		display: block;
		margin-bottom: 8px;
		font-weight: 700;
		font-size: 18px;
		line-height: 150%;
		color: var(--gray900-color);
	}

	&__time {
		font-weight: 400;
		font-size: 14px;
		line-height: 150%;
		color: var(--gray700-color);
	}

	&__right {
		width: 630px;
	}

	&__body {
		margin-bottom: 21px;
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray800-color);

		* {
			margin: 0;
			margin-bottom: 20px;

			&:last-child {
				margin: 0;
			}
		}

		a {
			font-weight: 700;
			color: var(--orange-color);

			&:focus {
				outline: 2px solid var(--orange-color);
				outline-offset: 2px;
			}

			&:hover {
				color: var(--active-color);
			}
		}
	}

	&__reply {
		display: inline-flex;
		font-weight: 400;
		font-size: 14px;
		line-height: 150%;
		color: var(--gray700-color);
		transition: color 0.3s ease-in-out;

		&:focus {
			outline: 2px solid var(--orange-color);
			outline-offset: 2px;
		}

		svg {
			display: block;
			margin-right: 8px;
			width: 16px;
			height: 16px;
			fill: currentColor;
			object-fit: cover;
		}

		&:hover {
			color: var(--orange-color);
		}

		&:active {
			color: var(--active-color);
		}
	}
}