// если вы хотите использовать sass-переменные - удалите root
:root {
	// base
	--font-family: "Ubuntu", sans-serif;
	--content-width: 1230px;
	--container-offset: 15px;
	--container-width: calc(var(--content-width) + (var(--container-offset) * 2));

	// colors
	--light-color: #FFFFFF;
	--gray900-color: #1E212C;
	--gray800-color: #424551;
	--gray700-color: #787A80;
	--gray200-color: #F4F5F7;
	--orange-color: #FF5A30;
	--active-color: #FC3300;

	// sizes
	--grid-gap: 30px;

	// speed
	--hero-slider-speed: 500;
}