.post {
	padding-bottom: 180px;

	&__container {}

	&__cover {
		position: relative;
		display: block;
		z-index: 5;
		margin-top: -79px;
		margin-bottom: 80px;
		object-fit: cover;
		width: 100%;

		@include mobile {
			margin-bottom: 40px;
		}
	}

	&__wrapper {
		margin: 0 auto;
		padding: 0 15px;
		max-width: 840px;

		@include mobile {
			padding: 0;
		}
	}

	&__content {
		padding-bottom: 60px;

		* {
			margin-bottom: 25px;
			font-size: 16px;
			line-height: 160%;
			color: var(--gray800-color);

			&:last-child {
				margin-bottom: 0;
			}
		}

		h2 {
			margin: 0;
			margin-bottom: 25px;
			font-weight: 700;
			font-size: 20px;
			line-height: 150%;
			color: var(--gray900-color);

			@include mobile {
				text-align: center;
				font-size: 18px;
			}
		}

		h3 {
			margin: 0;
			margin-bottom: 25px;
			font-weight: 700;
			font-size: 18px;
			line-height: 140%;
			color: var(--gray900-color);
		}

		p {
			margin: 0;
			font-weight: 400;
			font-size: 16px;
			line-height: 160%;
			color: var(--gray800-color);

			&:not(:last-child) {
				margin-bottom: 28px;
			}

			@include mobile {
				text-align: center;
				font-size: 14px;
			}
		}

		blockquote {
			position: relative;
			margin: 0;
			margin-bottom: 28px;
			padding: 20px;
			padding-left: 57px;
			font-weight: 700;
			font-size: 20px;
			line-height: 150%;
			color: var(--gray900-color);

			&::before {
				content: '';
				position: absolute;
				left: 0;
				top: 20px;
				width: 28px;
				height: 20px;
				background-image: url('/img/achieve/braces.svg');
				background-position: center;
				background-size: cover;
				background-repeat: no-repeat;

				@include mobile {
					width: 22px;
					height: 15px;
				}
			}

			@include mobile {
				padding-left: 30px;
				padding-right: 0;
				font-size: 18px;
			}
		}

		ul {
			margin: 0;
			margin-bottom: 24px;
			padding: 0;
			list-style: none;

			li {
				position: relative;
				padding-left: 32px;
				font-weight: 400;
				font-size: 16px;
				line-height: 160%;
				color: var(--gray800-color);

				&::before {
					content: '';
					position: absolute;
					left: 0;
					top: 4px;
					width: 16px;
					height: 16px;
					background-image: url('/img/decision-check.svg');
					background-position: left center;
					background-size: cover;
					background-repeat: no-repeat;
				}

				&:not(:last-child) {
					margin-bottom: 4px;
				}
			}
		}
	}

	&__share {
		display: flex;
		align-items: center;
		font-weight: 700;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray900-color);

		span {
			margin-right: 12px;
		}

		svg {
			fill: var(--gray700-color);
			opacity: 1;
		}
	}

	&__social {

		svg {
			transition: fill 0.3s ease-in-out;

			&:hover {
				fill: var(--orange-color);
			}

			&:active {
				color: var(--active-color);
			}
		}
	}

	@include tablet {
		padding-top: 60px;
		padding-bottom: 100px;
	}
}