.decisions {
	padding-top: 80px;
	padding-bottom: 80px;
	background-color: #F4F5F7;

	&__container {
		@include grid;
	}

	&__left {
		grid-column: 5 span;
	}

	&__image {
		position: relative;
		margin-bottom: -120px;
		z-index: 5;

		@include mobile {
			margin-bottom: 0;
		}
	}

	&__right {
		grid-column: 7 span;
		padding-left: 104px;

		@include tablet {
			padding-left: 30px;
		}

		@include small-tablet {
			padding-left: 0;
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 60px;

		@include mobile {
			margin-bottom: 40px;
		}
	}

	&__item {
		position: relative;
		padding-left: 38px;
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray800-color);

		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 20px;
			height: 20px;
			background-image: url('/img/decision-check.svg');

			@include bg-cover;
		}

		&:not(:last-child) {
			margin-bottom: 20px;
		}
	}
}