.related-projects {
	padding-top: 180px;
	padding-bottom: 122px;
	margin-top: -25px;
	background-color: var(--light-color);

	&__container {
		overflow: hidden;
	}

	&__top {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		margin-bottom: 47px;

		@include mobile {
			display: block;
			margin-bottom: 30px;
		}
	}

	&__title {
		margin: 0;
		padding-bottom: 12px;
		max-width: 710px;
	}

	&__items {
		margin: -100px;
		padding: 100px;

		.portfolio-item {
			@include mini-desktop {
				height: 100%;
			}

			&__descr {
				margin-bottom: 20px;
			}

			&__btn {
				margin-top: auto;
			}
		}
	}

	&__learn {
		margin-top: 80px;
	}

	@include tablet {
		padding-top: 100px;
		padding-bottom: 80px;
	}

	@include mobile {
		padding-top: 60px;
	}
}