.post-form {

	&__title {
		margin: 0;
		margin-bottom: 47px;
	}

	&__content {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-bottom: 48px;

		@include mobile {
			display: block;
		}
	}

	&__field {
		width: 48%;

		.form-field__caption {
			font-size: 16px;
		}

		.form-field__input {
			height: 52px;
			font-size: 16px;
		}

		&--big {
			margin-top: 24px;
			width: 100%;

			.form-field__input {
				height: 100px;
			}
		}

		@include mobile {
			margin-bottom: 20px;
			width: 100%;
		}
	}
}