.how-we-do {
	padding-bottom: 120px;

	&__container {}

	&__title {
		margin: 0;
		margin-bottom: 60px;
	}

	&__list {
		@include grid;
		counter-reset: section;

		@include tablet {}
	}

	&__item {
		position: relative;
		display: flex;
		flex-direction: column;
		grid-column: 3 span;

		&::before {
			counter-increment: section;
			content: "0" counter(section);
			margin-bottom: 22px;
			font-weight: 700;
			font-size: 46px;
			color: #D7DADD;
		}

		&:not(:last-child) {

			&::after {
				content: '';
				position: absolute;
				right: 0;
				top: 28px;
				width: 75%;
				height: 1px;
				background-image: url('/img/how-we-do-line.svg');

				@include bg-cover;


				@include tablet {
					display: none;
				}
			}
		}

		@include tablet {
			grid-column: 6 span;
		}
	}

	&__subtitle {
		margin: 0;
		margin-bottom: 9px;
		font-weight: 700;
		font-size: 20px;
		line-height: 150%;
		color: var(--gray900-color);

		@include mobile {
			font-size: 18px;
		}
	}

	&__descr {
		margin: 0;
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;
		color: var(--gray700-color);

		@include mobile {
			font-size: 14px;
		}
	}

	@include tablet {
		padding-bottom: 60px;
	}
}