.footer {
	padding: 80px 0;
	color: rgba(255, 255, 255, 0.6);
	background-color: var(--gray900-color);

	&__container {
		display: grid;
		grid-template-columns: repeat(12, 1fr);
		gap: 30px;

	}

	&__logo {
		margin-right: 60px;

		&:focus {
			outline: 1px solid var(--orange-color);
			outline-offset: 2px;
		}

		@include mobile {
			display: inline-block;
			margin-right: 0;
			margin-bottom: 30px;
		}
	}

	&__left {
		grid-column: 7 span;

		@include small-tablet {
			grid-column: 10 span;
		}

		@include mobile {
			grid-column: 10 span;
		}
	}

	&__top {
		display: flex;
		align-items: center;
		margin-bottom: 22px;

		@include small-tablet {
			justify-content: space-between;
		}

		@include mobile {
			display: block;
		}
	}

	&__social {
		align-items: flex-start;
	}

	&__form {
		grid-column: 5 span;

		@include small-tablet {
			grid-column: 10 span;
		}

		@include mobile {
			grid-column: 10 span;
		}
	}

	&__address {
		grid-column: 4 span;

		@include mobile {
			grid-column: 10 span;
		}
	}

	&__nav {
		grid-column: 8 span;

		@include mobile {
			grid-column: 10 span;
			padding-left: 0 !important;
		}
	}

	&__descr {
		margin: 0;
		width: 100%;
		max-width: 495px;
		font-weight: 400;
		font-size: 14px;
		line-height: 150%;
	}

	&__copyright {
		grid-column: 12 span;

	}
}

.overlay {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	z-index: 500;
	background-color: rgba(#000, 0.6);
	opacity: 0;
	visibility: hidden;
	transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

	&--active {
		opacity: 1;
		visibility: visible;
	}
}

.footer-form {

	&__title {
		margin: 0;
		margin-bottom: 24px;
		font-weight: 700;
		font-size: 24px;
		line-height: 150%;
		color: var(--light-color);
	}

	&__items {
		display: flex;
		align-items: flex-start;
	}

	&__btn {
		padding: 11px 24px;
		border-radius: 0px 4px 4px 0px;
		font-weight: 700;
		font-size: 14px;
		letter-spacing: 0.5px;

		@include mobile {
			padding: 11px 10px;
		}
	}

	&__field {
		margin-bottom: 16px;

		@include mobile {
			width: 100%;
		}
	}

	&__descr {
		margin: 0;
		font-weight: 400;
		font-size: 12px;
		line-height: 150%;
	}

}

.footer-address {
	padding-top: 19px;
	margin-bottom: 29px;

	&__title {
		margin-bottom: 15px;
	}

	&__item {
		display: block;
		font-weight: 400;
		font-size: 16px;
		line-height: 160%;

		&:not(:last-child) {
			margin-bottom: 4px;
		}

		a {
			color: inherit;
			transition: color 0.3s ease-in-out;

			&:focus {
				outline: 1px solid var(--orange-color);
				outline-offset: 2px;
			}

			&:hover {
				color: var(--orange-color);
			}

			&:active {
				color: var(--active-color);
			}
		}
	}

	&__name {
		color: var(--light-color);
	}


}

.footer-nav {
	display: flex;
	padding-top: 19px;
	padding-left: 54px;

	&__col {
		&:not(:last-child) {
			margin-right: 125px;

			@include small-tablet {
				margin-right: 50px;
			}
		}
	}

	&__title {
		margin-bottom: 17px;

		@include mobile {
			font-size: 14px;
		}
	}

	&__list {}

	&__item {
		&:not(:last-child) {
			margin-bottom: 11px;
		}

		a {
			transition: color 0.3s ease-in-out;

			&:focus {
				outline: 1px solid var(--orange-color);
				outline-offset: 2px;
			}

			&:hover {
				color: var(--orange-color);
			}

			&:active {
				color: var(--active-color);
			}
		}
	}

	&__link {
		color: rgba(255, 255, 255, 0.6);

		a {
			color: inherit;
		}
	}

	@include small-tablet {
		padding-left: 30px;
	}

	@include mobile {
		padding-top: 0;
	}

}

.footer__copyright {
	font-weight: 400;
	font-size: 12px;
	line-height: 150%;
	color: var(--light-color);

	span {
		padding-left: 22px;
		background-image: url('/img/footer/heart.svg');
		background-repeat: no-repeat;
		background-size: 16px 16px;
		background-position: 2px center;

		svg {
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

.to-top {
	position: fixed;
	right: calc((100% - 1230px) / 2);
	bottom: 80px;
	z-index: 50;
	display: flex;
	align-items: flex-end;
	opacity: 0;
	visibility: hidden;
	transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

	&__text {
		// display: block;
		margin-right: 16px;
		font-weight: 400;
		font-size: 14px;
		line-height: 150%;
		color: var(--light-color);
		text-transform: uppercase;
	}

	&__icon {
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 4px;
		width: 40px;
		height: 40px;
		background-color: var(--orange-color);
		fill: var(--light-color);
		transition: background-color 0.3s ease-in-out;

		svg {
			width: 24px;
			height: 24px;
			object-fit: cover;
		}

		&:hover {
			background-color: var(--active-color);
		}
	}

	&--active {
		opacity: 1;
		visibility: visible;
	}

	@include big-desktop {
		right: 20px;
		bottom: 20px;
	}
}